// tslint:disable-next-line:no-var-requires
const FileSaver = require("file-saver");
import QueueAnim from "rc-queue-anim";
import React from "react";
import style from "./index.scss";
class About extends React.Component {
  public render() {
    return (
      <div className={style.container}>
        <QueueAnim
          type="right"
          ease="easeInOutQuart"
        >
          <section key="AboutMe">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <div>
                
              </div>
              <div>
                2017
              </div>
              <div>
                
              </div>
              <div></div>
              <div></div>
              <div data-flex='cross:center'><img className={style.resume} onClick={this.downloadResume} src="img/assets/pdf-icon.png" alt="" /></div>
            </div>
          </section>

          <section key="ContactMe">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <p>
                <i className="iconfont icon-qq" />
                <span>122760248</span>
              </p>
              <p>
                <i className="iconfont icon-weixin" />
                <span>unbolibobo</span>
              </p>
              <p>
                <i className="iconfont icon-github" />
                <span>https://github.com/Bendjen</span>
              </p>
              <p>
                <i className="iconfont icon-email" />
                <span>unbolibobo@foxmail.com</span>
              </p>
            </div>
          </section>

          <section key="MyProject">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <div className={style.projectItem}>
                <h2></h2>
                <p><a href="https://bendjen.github.io/parcel-ts#home/idea" target="_blank">https://bendjen.github.io/parcel-ts</a></p>
                <p>Github<a href="https://github.com/Bendjen/parcel-ts" target="_blank">https://github.com/Bendjen/parcel-ts</a></p>
                <p>reacttypescriptrxjsantd</p>
                <p data-flex='main:center cross:center'>
                  <img src="img/project/parcel-ts.gif" alt="" />
                </p>
              </div>
            </div>
            <div className={style.content} style={{marginTop:40}}>
              <div className={style.projectItem}>
                <h2>Piecework-Manager/PC</h2>
                <p><a href="https://bendjen.github.io/Piecework-Manager-pc?MOCK_FLAG=true" target="_blank">https://bendjen.github.io/Piecework-Manager-pc</a></p>
                <p>Github<a href="https://github.com/Bendjen/Piecework-Manager-pc" target="_blank">https://github.com/Bendjen/Piecework-Manager-pc</a></p>
                <p>vuetypescriptg2element-ui</p>
                <p data-flex='main:center cross:center'>
                  <img src="img/project/piecework-manager-pc.gif" alt="" />
                </p>
              </div>
            </div>
            <div className={style.content} style={{marginTop:40}}>
              <div className={style.projectItem}>
                <h2>Chat-Room</h2>
                <p>Github<a href="https://github.com/Bendjen/Piecework-Manager-pc" target="_blank">https://github.com/Bendjen/Piecework-Manager-pc</a></p>
                <p>node.jssocket.io</p>
                <p data-flex='main:center cross:center'>
                  <img src="img/project/chat-room.gif" alt="" />
                </p>
              </div>
            </div>
            <div className={style.content} style={{marginTop:40}}>
              <div className={style.projectItem}>
                <h2>Piecework-Manager/Mobile</h2>
                <p><a href="https://bendjen.github.io/Piecework-Manager-mobile" target="_blank">https://bendjen.github.io/Piecework-Manager-mobile</a></p>
                <p>Github<a href="https://github.com/Bendjen/Piecework-Manager-mobile" target="_blank">https://github.com/Bendjen/Piecework-Manager-mobile</a></p>
                <p>vueparcelechartsvant</p>
                <p data-flex='main:center cross:center'>
                  <img src="img/project/piecework-manager-mobile.gif" alt="" />
                </p>
              </div>
            </div>
          </section>

          <section key="AboutSite">
            <h1 className={style.title}></h1>
            <div className={style.content}>
              <div>
                
                <a target="_blank" href="https://bendjen.github.io/parcel-embryo">
                  https://bendjen.github.io/parcel-embryo
                </a>
              </div>
              <div> reactwebpacktypescriptantd</div>

              <div>
                
                <ol>
                  <li>
                    reduxwebpackrequire.context()
                  </li>
                  <li>
                    homedetaimarkabout
                  </li>
                  <li>
                    typeScriptreact-app-rewired
                  </li>
                </ol>
              </div>

              <div>
                
                
              </div>
            </div>
          </section>
        </QueueAnim>
      </div>
    );
  }
  private downloadResume() {
    FileSaver.saveAs("/parcel-ts/resume/.pdf", ".pdf");
  }
}

export default About;



